* {
    border: 0px;
    margin: 0px;
}

.top-section {
    /* height: 20vh; */
    align-items: center;
    text-align: center;
    display: flex;
    vertical-align: middle;
    flex-direction: column;
    justify-content: center;
    /* background-color: red; */
    background: url("../images/books-background.jpg") repeat fixed 50%;
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;

    margin-bottom: 10px white;
}

.top-section h1 {
    margin: 60px 0px 20px 0px;

}

.top-section h6 {
    margin: 0px 0px 60px 0px;
}


/* genre-items  */

.genres h1 {
    margin: 30px;
}

.genres-items {
    margin: 30px 30px;
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    /* margin: 0px 50px; */
}

.genres-items .item {
    height: 150px;
    width: 250px;
    margin: 20px;
    display: flex;
    align-items: center;
    justify-content: center;
    color: white;
    /* background: url(../images/items-image-1.jpg); */
}
.heading
{
    width: 250px;
    background-color: black;
    opacity: 0.6;
    display: flex;
    justify-content: center;
    align-items: center;
}
.heading:hover
{
    opacity: 0.9;
}
.genres-items .item h2 
{
    /* opacity: 0.7; */
    display: block;
    width: auto;
    padding: 15px 30px;
}



