* {
    border: 0px;
    margin: 0px;
}

.search-bar {
    height: 20vh;
    background-color: black;
    align-items: center;
    display: flex;
    justify-content: right;

}

input {
    margin: 50px;
    height: 30px;
    width: 200px;
    border-radius: 50px;
    padding: 0px 15px;
}

.box {
    width: 100px;
    padding: 5px;
    margin: 3px;
    border: 1px solid white;
    text-align: center;
    background-color: black;
    /* display: fixed; */

}

.search-bar . {
    display: flex;
    justify-content: center;
}


.search-bar .options {
    display: flex;
    align-items: center;
    justify-self: center;
    flex-direction: column;
}


.sidebar-heading 
{
    display: flex;
    text-align: center;
    justify-content: center;
}