html,
body {
  margin: 0;
  background-color: #0F3D3E;
  color: white;
}

h1 {
  text-align: center;
}

#App {
  font-family: sans-serif;
}

.bm-item {
  display: inline-block;

  /* Our sidebar item styling */
  text-decoration: none;
  margin-bottom: 10px;
  color: #d1d1d1;
  transition: color 0.2s;
}

.bm-item:hover {
  color: white;
}

.bm-burger-button {
  position: fixed;
  width: 36px;
  height: 30px;
  left: 20px;
  top: 10px;
}

/* Color/shape of burger icon bars */
.bm-burger-bars {
  background: white;
}

/* Position and sizing of clickable cross button */
.bm-cross-button {
  height: 50px;
  width: 50px;
}

/* Color/shape of close button cross */
.bm-cross {
  background: white;
  font-size: 1em;
}

/* General sidebar styles */
.bm-menu {
  /* background: #000; */
  background: #2C3333;
  padding: 2.5em 1.5em 0;
  font-size: 1.5em;
}

/* Morph shape necessary with bubble or elastic */
.bm-morph-shape {
  fill: #373a47;
}

/* Wrapper for item list */
.bm-item-list {
  color: #b8b7ad;

}

.bm-item-list a {
  padding: 0.1em 1em;
}

.bm-item-list a:hover {
  background: #395B64;
  /* outline: 1px solid white; */
  /* border-radius: 20px; */
}

/* Styling of overlay */
.bm-overlay {
  background: rgba(0, 0, 0, 0.3);
}


.genres-headings {
  text-align: center;
  width: 100%;
  margin: 30px;
}

.books {
  display: flex;
  justify-content: center;
  align-items: center;
  color: black;
}

.card {
  text-align: center;
  display: flex;
  justify-self: center;
}

.individual-books-card {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  background-color: #182802;
  border: 3px solid white;
  border-radius: 10px;
  width: 400px;
  padding: 0px;
}

.genres-headingg {
  /* color: pink; */
